<template>
    <div v-if="!item.hidden" class="menu-item-container">
      <el-menu-item v-if="hasOneShowingChild(item.children,item) && onlyOneChild" :index="resolvePath(onlyOneChild.path)">
        <i :class="onlyOneChild.meta && ['el-icon-' + onlyOneChild.meta.icon]"></i>
        <span v-if="!isCollapse || isNest">{{onlyOneChild.meta && onlyOneChild.meta.title}}</span>
      </el-menu-item>

      <el-submenu v-else-if="item.meta" :index="resolvePath(item.path)">
        <template slot="title">
          <i :class="item.meta && ['el-icon-' + item.meta.icon]"></i>
          <span v-if="!isCollapse"> {{item.meta && item.meta.title}}</span>
        </template>
        <SidebarItem
          v-for="child in item.children"
          :key="child.path"
          :is-nest="true"
          :item="child"
          :base-path="resolvePath(child.path)"
        />
      </el-submenu>

      <SidebarItem
        v-else
        v-for="child in item.children"
        :key="child.path"
        :is-nest="item.meta"
        :item="child"
        :base-path="resolvePath(child.path)"
      />
    </div>
</template>

<script>
import path from 'path';
import { isExternal } from '../../../common/utils';
import { mapGetters } from 'vuex';
export default {
  name: 'SidebarItem',
  props: {
    // route object
    item: {
      type: Object,
      required: true
    },
    isNest: {
      type: Boolean,
      default: false
    },
    basePath: {
      type: String,
      default: ''
    }
  },
  data () {
    // To fix https://github.com/PanJiaChen/vue-admin-template/issues/237
    // TODO: refactor with render function
    return {
      onlyOneChild: null
    }
  },

  computed: {
    ...mapGetters([
      'permissionRoutes',
      'sidebar'
    ]),

    isCollapse () {
      return !this.sidebar.opened
    }
  },
  methods: {
    hasOneShowingChild (children = [], parent) {
      const showingChildren = children.filter(item => {
        if (item.hidden) {
          return false
        } else {
          // Temp set(will be used if only has one showing child)
          this.onlyOneChild = item
          return true
        }
      })

	    if (showingChildren.length === 1 && showingChildren[0].parentControl) {
		    this.onlyOneChild = parent
		    return true
	    }

      // Show parent if there are no child router to display
      if (showingChildren.length === 0) {
        this.onlyOneChild = { ...parent, path: '', noShowingChildren: true }
        return true
      }
      return false
    },
    resolvePath (routePath) {
      if (isExternal(routePath)) {
        return routePath
      }
      if (isExternal(this.basePath)) {
        return this.basePath
      }
      return path.resolve(this.basePath, routePath)
    }
  }
}
</script>

<style lang='scss' scoped>
.menu-item-container{
  .el-menu-item{
    padding-left: 20px;
  }
}
</style>




